<template>
  <div>
  
    <div class="guanliyuan-middle">
      <span>{{data.length}}</span>
      <div class="right">
        <a-button icon="folder-add"></a-button>
      </div>
    </div>
    <div class="right-content2-detail">
      <a-table
    
        :columns="columns"
        :dataSource="data"
        bordered
        :pagination="{ pageSize: 8 }"
      >
        <a slot="3" href="javascript:;">
          <span class="table-shenhe mr10"></span>
          <span class="table-shenhe"></span>
        </a>
      
      </a-table>
    </div>
  </div>
</template>

<script>
export default {
  name: "yinhangguanli",
  data() {
    return {
      data: [],
      columns: []
    };
  },
  mounted() {
    // 
    this.columns = [
      {
        title: "ID",
        dataIndex: "1",

        scopedSlots: { customRender: "1" }
      },
      {
        title: "",
        dataIndex: "2",

        scopedSlots: { customRender: "2" }
      },
      {
        title: "",
        dataIndex: "3",

        scopedSlots: { customRender: "3" }
      }
    ];
    this.data = [];
    for (let i = 0; i < 30; i++) {
      this.data.push({
        1: `1` + i + "",
        2:''
      });
    }
  },

  methods: {
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
</style>



