        dataIndex: "12",
        scopedSlots: { customRender: "12" }
      }
    ];
    this.data = [];
    for (let i = 0; i < 100; i++) {
      this.data.push({
        0: i + 1,
        1: "2019-10-09 14:21:22",
        2: `135` + i + "1267349",
        3: "11723" + i,
        4: "94563" + i + "1" + i,
        5: "LV0",
        6: "",
        7: "",
        8: "",
        9: "220304387249879579",
        10: "",
        11: "2019-10-09 14:21:22"
      });
    }
  
  },
  methods: {
        handleChange(value, key, column) {
      const newData = [...this.data];
      const target = newData.filter(item => key === item.key)[0];
      if (target) {
        target[column] = value;
        this.data = newData;
      }
    }
  }
};
</script>

