import xitongmsgData from "./childComponents/xitongMsg";
import tixianguanliData from "./childComponents/tixianguanli";
import maijiaguanliData from "./childComponents/maijiaguanli";
import heimingdanguanliData from "./childComponents/heimingdanguanli";
import tuanduishezhiData from "./childComponents/tuanduishezhi";
import mytuanduiData from "./childComponents/mytuandui";
import danliangtongjiData from "./childComponents/danliangtongji";
import shujvtongjiData from "./childComponents/shujvtongji";
import zijintongjiData from "./childComponents/zijintongji";
import fenzhantongjiData from "./childComponents/fenzhantongji";
import zhanghaoxinxiData from "./childComponents/zhanghaoxinxi";
import pianzikuData from "./childComponents/pianziku";
import fenzhanguanliData from "./childComponents/fenzhanguanli";
import shensuzhongxinData from "./childComponents/shensuzhongxin";
import yinhangguanliData from "./childComponents/yinhangguanli";
export default {
  name: "management",
  data() {
    return {
      showMenu: false,
      showUser: false,
      showShop: false,
      showData: false,
      showSetting: false,
      showDataMsg: false,
      showQuanxian:false,
      listUnit: 1,
      listUnit1: 5,
      listUnit2: 7,
      data: [],
      targetKey: "",
      columns: [],
      activeKey: "",
      newTabIndex: 0,
      panes: [],
      indexColumns: [],
      indexData: []
    };
  },
  components: {
    guanliyuanData,
    yonghuguanliData,
    jueseguanliData,
    quanxianData,
    quanxianyonghuData,
    quanxianjueseData,
    caidanguanliData,
    daimaData,
    xitongsettingData,
    xitongmsgData,
    tixianguanliData,
    maijiaguanliData,
    heimingdanguanliData,
    tuanduishezhiData,
    mytuanduiData,
    danliangtongjiData,
    shujvtongjiData,
    zijintongjiData,
    fenzhantongjiData,
    zhanghaoxinxiData,
    pianzikuData,
    fenzhanguanliData,
    shensuzhongxinData,
    yinhangguanliData
  },
  mounted() {
    // 
    this.panes = [{ title: "", content: "", key: "", closable: false }];
    // 
    this.indexColumns = [
      {
        title: "",
        dataIndex: "name"
      },
      {
        title: "",
        dataIndex: "age"
      },
      {
        title: "",
        dataIndex: "address"
      },
      {
        title: "",
        dataIndex: "detail"
      }
    ];
    this.indexData = [
      {
        key: "1",
        name: "",
        age: "",
        address: "",
        detail: ""
      },
      {
        key: "2",
        name: "Linux",
        age: "dhxtdhxtsrcassetsimgs",
        address: "/",
        detail: "12.34GB/39.23GB 26.03GB"
      }
    ];
  },
  methods: {
    showList(param) {
      if (param == "showUser") {
        this.showUser = !this.showUser;
      }
      if (param == "showShop") {
        this.showShop = !this.showShop;
      }
      if (param == "showData") {
        this.showData = !this.showData;
      }
    },
    callback(targetKey) {
      console.log("callback", targetKey);
      this.targetKey = targetKey;
    },
    onEdit(targetKey, action) {
      this[action](targetKey);
    },
    add(title) {
      const panes = this.panes;
      var flag = 0;
      const activeKey = title;
      panes.forEach(item => {
        if (title === item.title) {
          flag++;
          this.activeKey = activeKey;
        }
      });
      if (flag == 0) {
        if (title == "") {
          panes.push({
            title: title,
            content: "",
            key: activeKey
          });
        } else {
          panes.push({
            title: title,
            content: "",
            key: activeKey
          });
        }
        this.panes = panes;
        this.activeKey = activeKey;
        console.log("add", this.activeKey);
      }
    },
    remove(targetKey) {
      let activeKey = this.activeKey;
      let lastIndex;
      this.panes.forEach((pane, i) => {
        if (pane.key === targetKey) {
          lastIndex = i - 1;
        }
      });
      const panes = this.panes.filter(pane => pane.key !== targetKey);
      if (panes.length && activeKey === targetKey) {
        if (lastIndex >= 0) {
          activeKey = panes[lastIndex].key;
        } else {
          activeKey = panes[0].key;
        }
      }
      this.panes = panes;
      this.activeKey = activeKey;
      console.log("removw", activeKey);
    },
    handleChange(value, key, column) {
      const newData = [...this.data];
      const target = newData.filter(item => key === item.key)[0];
      if (target) {
        target[column] = value;
        this.data = newData;
      }
    }
  }
};
</script>

